.container {
  display: grid;
  width: 100%;

  grid-template-columns: repeat(auto-fill, minmax(70px, 70px));
  grid-auto-rows: minmax(70px, 70px);
}

.item {
  border: 1px solid rgba(0, 0, 0, 0.5);
}

.clicked {
  background-color: blue;
}
.img {
  object-fit: cover;

}

.img:hover {
  object-fit: cover;
  transform: scale(1.5);
  transition: transform.5s;
}

.span1 {
  grid-row-end: span 1;
  grid-column-end: span 1;
}

.span2 {
  grid-row-end: span 2;
  grid-column-end: span 2;
}

.span3 {
  grid-row-end: span 3;
  grid-column-end: span 3;
}

.span4 {
  grid-row-end: span 4;
  grid-column-end: span 4;
}

.span5 {
  grid-row-end: span 5;
  grid-column-end: span 5;
}